@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
body {
  margin: 0;
  font-family:'Architects Daughter';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Header*/

header {
    width: 100%;
    background-color: #270f0ff2;
    position: fixed;
    z-index: 2;
}

.header-nav {
    width: 80%;
    height: 100px;
    display: flex;
    justify-content: center;
    margin: auto;
}

.header-nav img {
    width: 100px;
}

.header-nav ul {
    display: flex;
    align-items: center;
    list-style: none;
    margin: auto;
}

.header-nav li {
    padding: 0 0 0 110px;
}

.header-nav a {
    text-decoration: none;
    color: #cf7f57;
    font-size: 26px;
    cursor: pointer;
}

.header-nav a:hover {
    text-decoration: underline;
    color: #cf7f57;
}

.nav-mobile {
    display: none;
}

.nav-mobile ul {
    list-style: none;
}

.nav-mobile a {
    text-decoration: none;
    color: #cf7f57;
    font-size: 22px;
}


/*Parallax*/
.intro {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background: linear-gradient(
            rgba(0, 0, 0, 0.5),
            rgba(0, 0, 0, 0.5)
    );
}

.headline {
    font-size: 130px;
    text-align: center;
    text-transform: capitalize;
    color: #ffffff;
    text-shadow: 2px 3px #181717;
}

.parallax {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100%;
    max-height: 110%;
    height: 110%;
    object-fit: cover;
    z-index: -1;
}

/*About*/

.details {
    background-color: #fffbf9;
    display: flex;
    flex-direction: column;
    padding: 50px 0;
}

.details h3 {
    color: #A95534;
    font-size: 50px;
    text-align: center;
    text-shadow: 1px 2px #181717;
}

.details .wrapper, .details .reverse-wrapper {
    width: 80%;
    display: flex;
    margin: auto;
}

.details-text {
    color: #bc633f;
    margin: auto;
}

.details-text h4 {
    font-size: 32px;
    text-align: center;
    text-shadow: 1px 1px #181717;
}

.details-text p {
    color: #773a23;
    font-size: 24px;
    width: 75%;
    padding: 0 0 0 100px;
}

.preview{
    width: 350px;
    height: auto;
    border-radius: 50%;
    border: 2px solid #8281815e;
    margin: 20px auto;
    box-shadow: 1px 5px 22px -4px #000000;
}

/*Gallery*/

.alice-carousel__dots, .alice-carousel__prev-btn, .alice-carousel__next-btn {
    display: none;
}

.alice-carousel__wrapper {
    padding: 100px 0;
}

.item  img{
    width: 95%;
    height: auto;
    display: block;
    margin: auto;
    box-shadow: 1px 5px 22px -4px #000000;
}


/*Address*/

.address {
    height: 818px;
    padding: 50px 0;
}

.ref {
    width: 100%;
    height: 700px;
    position: relative;
}

.address h3 {
    color: #A95534;
    font-size: 50px;
    text-transform: capitalize;
    text-align: center;
    text-shadow: 1px 2px #181717;
}

.address .info-wrapper {
    width: 60%;
    height: 280px;
    left: 0;
    top: -490px;
    position: relative;
    display: flex;
    margin: auto;
    justify-content: space-evenly;
    background: linear-gradient( rgb(0 0 0 / 62%), rgb(0 0 0 / 62%) );
}

.address .info-wrapper h4 {
    color: #ffffff;
    font-size: 32px;
    text-align: center;
    text-shadow: 1px 1px #181717;
}

.address .info-wrapper p {
    color: #ffffff;
    font-size: 24px;
    text-shadow: 1px 1px #181717;
}




/*Menu*/

.tabs-wrapper {
    height: auto;
    padding: 100px 0;
}

.tabs {
    margin: 100px 0 0;
    display: flex;
    justify-content: space-evenly;
    cursor: pointer;
}

.tab-item {
    color: #cf7f57;
    font-size: 24px;
    font-weight: bold;
    text-shadow: 1px 1px #181717;
    background-color: #f3cdbbab;
    border: 2px solid #270f0fc2;
    border-radius: 0;
    margin: 0;
    padding: 20px 30px;
    list-style: none;
}

.tab-item:hover {
    background-color: #270f0ff2;
}

.react-tabs__tab--selected {
    background-color: #270f0ff2;
}

.lunch {
    display: block;
    text-align: center;
}

.burgers ul, .crepes ul {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    padding: 0;
}

.burgers li, .crepes li {
    list-style: none;
    flex: 0 0 33.333333%;
    margin: auto;
}

.burgers img, .crepes img {
    width: 50%;
    height: 320px;
    object-fit: cover;
    border: 2px solid #8281815e;
    border-radius: 50%;
    margin: 20px auto;
    box-shadow: 1px 5px 22px -4px #000000;
}

.lunch div {
    display: flex;
    justify-content: space-evenly;
}

.lunch img {
    width: 40%;
    height: auto;
    object-fit: cover;
    border: 2px solid #8281815e;
    margin: 20px auto;
    box-shadow: 1px 5px 22px -4px #000000;
}

.burgers h4, .lunch h4, .crepes h4 {
    color: #A95534;
    font-size: 36px;
    text-shadow: 1px 2px #181717;
}

.burgers span, .lunch span, .crepes span {
    color: #A95534;
    font-size: 30px;
    background-color:#f3cdbbab;
    border-radius: 50%;
    padding: 20px;
    font-weight: bold;
    text-shadow: 1px 2px #181717;
}
.burgers p, .lunch p, .crepes p {
    width: 80%;
    text-align: center;
    font-size: 20px;
    color: #773a23;
    font-weight: bold;
    padding-top: 30px;
    margin: auto;
} 

/*Footer*/

footer {
    background-color: #270f0ff2;
    height: 100px;
    display: flex;
    justify-content: center;
}

footer img {
    width: 100px;
}

footer div {
    display: flex;
}

.socials {
    display: flex;
    width: 50%;
    align-items: center;
    justify-content: center;
}

.socials a {
    padding: 0 20px;
}

.icon {
    color: white;
    font-size: 40px;
}

.icon:hover {
    color: #e29a78;
    font-size: 50px;
    transform: rotate(360deg);
    transition: all 0.4s ease-in-out;
}

footer span {
    color: #ffffff;
    font-size: 20px;
    margin: auto;
    text-shadow: 2px 3px #181717
}

@media only screen and (min-width: 1024px) and (max-width: 1400px){
    .nav-mobile {
        display: none;
    }
    .details .wrapper {
        padding: 20px 0;
    }
    .details-text p {
        width: 85%;
        padding: 0 0 0 50px;
    }
    .address .info-wrapper {
        width: 85%;
    }
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
    .header-nav {
        display: none;
    }
    .nav-mobile {
        width: 80%;
        height: 120px;
        display: flex;
        justify-content: space-between;
        margin: auto;
    }
    .nav-mobile img {
        width: 120px;
    }
    .mobile-menu .open-menu, .mobile-menu .close-menu {
        font-size: 30px;
        color: #fffbf9;
    }
    .nav-mobile .mobile-menu {
        margin: auto 0;
    }
    .parallax {
        width: 100%;
        height: auto;
    }
    .img-wrapper {
        display: flex;
        margin: auto;
    }
    .details .wrapper {
        width: 85%;
        display: block;
        padding: 20px 0;
    }
    .reverse-wrapper {
        display: flex;
        width: 85%;
        margin: auto;
        flex-direction: column-reverse;
        padding: 20px 0;
    }
    .details-text p {
        width: 100%;
        padding: 0;
    }
    .address .info-wrapper {
        width: 90%;
    }
    .address .info-wrapper p {
        font-size: 20px;
    }
    .lunch div {
        display: grid;
    }
    .lunch img {
        width: 80%;
    }
}

@media only screen and (min-width: 320px) and (max-width: 767px) {
    .header-nav {
        display: none;
    }
    .nav-mobile {
        width: 80%;
        height: 120px;
        display: flex;
        justify-content: space-between;
        margin: auto;
    }
    .nav-mobile img {
        width: 120px;
    }
    .nav-mobile .open-menu, .nav-mobile .close-menu {
        font-size: 30px;
        color: #fffbf9;
    }
    .nav-mobile .mobile-menu {
        margin: auto 0;
    }
    .parallax {
        width: 100%;
        height: auto;
    }
    .img-wrapper {
        display: flex;
        margin: auto;
    }
    .preview {
        width: 250px;
    }
    .details .wrapper {
        width: 85%;
        display: block;
        padding: 20px 0;
    }
    .reverse-wrapper {
        width: 85%;
        margin: auto;
        display: flex;
        flex-direction: column-reverse;
        padding: 20px 0;
    }
    .details-text p {
        width: 100%;
        padding: 0;
    }
    .address .info-wrapper {
        width: 80%;
        height: auto;
        top: -580px;
        display: block;
    }
    .address .info-wrapper p {
        font-size: 22px;
        text-align: center;
    }
    .lunch div {
        display: grid;
    }
    .lunch img {
        width: 80%;
    }
}

